    width: 100vw; height: 100vh;
    z-index: 11;
    .filter_gray{
        filter: grayscale(1);
    }
    .mask{
        background: radial-gradient(rgba(0, 0, 0, 0.5),rgb(0, 0, 0));
    }
    .sub_btn{
        background: url('../../assets/ui/ui\ \(128\).png')no-repeat;
        background-size: 100% 100%;
    }
    .add_btn{
        background: url('../../assets/ui/ui\ \(127\).png')no-repeat;
        background-size: 100% 100%;
    }
    .container{
        top: 50%;
        margin-top: -77.5vw;
        width: 100vw; height: 155vw;
        background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
        background-size: cover;
        .star_bar,.arms_bar{
            top: 24vw; left: 40vw;
            width: 55vw; height: 8vw;
            background: #1414144b;
            .red_list{
                left: 50%; top: 2vw;
                width: 19vw; height: 4vw;
                margin: 0 0 0 -9.5vw;
                li{
                    position: relative; float: left;
                    width: 3.8vw; height: 3.8vw;
                }
            }
            .sub_btn,.add_btn{
                top: 1.5vw; left: 5vw;
                width: 5vw; height: 5vw;
                line-height: 3.4vw;
                font-weight: bold;
                font-size: 5vw;
            }
            .add_btn{
                left: auto; right: 5vw;
            }
            .sub_btn.touch,.add_btn.touch{
                transform: scale(.8);
            }
        }
        .arms_bar{
            top: 35vw;
            .title{
                width: 18vw;height: 8vw;
                line-height: 8vw;
                color: rgb(216, 216, 216);
                font-weight: bold;
                .icon{
                    top: 1.5vw; left: 1.5vw;
                    width: 5vw; height: 5vw;
                }
            }
            .arms_icon{
                left: 18vw; top: 0;
                width: 40vw; height: 8vw;
                li{
                    position: relative; float: left;
                    width: 8vw; height: 8vw;
                    margin: 0 0 0 2vw;
                    
                }
                li.filter_gray{
                    filter: grayscale(1);
                }
            }
        }
        .level{
            top: 72vw; left: 5vw;
            width: 90vw;height: 8vw;
            background: #1414144b;
            .title{
                width: 24vw;height: 8vw;
                line-height: 8vw;
                color: rgb(216, 216, 216);
                font-weight: bold;
                .icon{
                    top: 1.5vw; left: 1.5vw;
                    width: 5vw; height: 5vw;
                }
            }
            .sub_btn,.add_btn{
                top: 2vw;
                width: 8vw; height: 4vw;
                line-height: 3.4vw;
                font-weight: bold;
                font-size: 5vw;
                background: rgb(180, 180, 180);
                color: black;
            }
            .sub_btn{
                left: 35vw;
            }
            .add_btn{
                left: 65vw;
            }
            .num{
                left: 48.5vw;
                width: 10vw;height: 8vw;
                line-height: 8vw;
            }
            .sub_btn.touch,.add_btn.touch,.empty_btn.touch,.full_btn.touch{
                transform: scale(.8);
            }
            
        }
        .attr{
            top: 83vw; left: 5vw;
            width: 90vw; height: 35vw;
            li{
                position: relative;
                width: 100%; height: 8vw;
                margin: 0 0 3vw 0;
                background: #1414144b;
                .title{
                    width: 24vw;height: 8vw;
                    line-height: 8vw;
                    color: rgb(216, 216, 216);
                    font-weight: bold;
                    .icon{
                        top: 1.5vw; left: 1.5vw;
                        width: 5vw; height: 5vw;
                    }
                }
                .empty_btn,.full_btn{
                    top: 2vw;
                    width: 8vw; height: 4vw;
                    line-height: 4vw;
                    background: rgb(117, 117, 117);
                    color: rgb(255, 255, 255);
                }
                .empty_btn{
                    left: 22vw;
                }
                .full_btn{
                    left: 78vw;
                }
                .sub_btn,.add_btn{
                    top: 2vw;
                    width: 8vw; height: 4vw;
                    line-height: 3.4vw;
                    font-weight: bold;
                    font-size: 5vw;
                    background: rgb(180, 180, 180);
                    color: black;
                }
                .sub_btn{
                    left: 35vw;
                }
                .add_btn{
                    left: 65vw;
                }
                .number{
                    left: 46.5vw;
                    width: 16vw;height: 8vw;
                    line-height: 8vw;
                    text-align: center;
                    // background: red;
                    span{
                        color: rgb(12, 213, 12);
                    }
                }
                .sub_btn.touch,.add_btn.touch,.empty_btn.touch,.full_btn.touch{
                    transform: scale(.8);
                }
            }
            li:nth-of-type(1){
                margin: 1vw 0 3vw 0;
            }
        }
        .remain_plus{
            top: 128vw; left: 5vw;
            width: 90vw; height: 8vw;
            text-align: left;
            line-height: 8vw;
            text-indent: 5vw;
            color: rgb(216, 216, 216);
            font-weight: bold;
            background: #1414144b;
        }
        .unload_warrior_btn{
            top: auto; bottom: 5vw; left: 5vw;
            width: 20vw; height: 6vw;
            background: white;
            color: black;
            text-align: center;
            line-height: 6vw;
            font-weight: bold;
        }
        .close_btn{
            left: auto;right: 4vw; top: 6vw;
            height: 8vw; width: 8vw;
            background: url('../../assets/ui/ui (85).png')no-repeat;
            background-size: 100% 100%;
        }
        .close_btn.click,.unload_warrior_btn.click{
            transform: scale(.8);
        }
        .topic{
            font-family: 'FZLBJW';
            top: 5vw; width: 55vw; height: 12vw;
            background: url('../../assets/background/bg (5).png')no-repeat;
            background-size: 100% 100%;
            line-height: 12vw;
            font-size: 5vw;
            text-indent: -16vw;
        }
        .warrior_img{
            left: 5vw; top: 23vw;
            width: 30vw; height: 45vw;
            
        }
    }
}
</style>